<template>
	<div class="user">
    <div class="user-search">
      <el-input placeholder="请输入用户名" v-model="username" :size="size" class="search-input"></el-input>
      <el-button type="primary" @click="search" :size="size" class="search-button">搜索</el-button>
    </div>
    <div class="user-table">
      <el-table :size="size"
                :data="userData"
                style="width: 100%" :height="twHeight - 170" class="table-list" border>
        <el-table-column prop="userId" label="用户id" header-align="center"></el-table-column>
        <el-table-column prop="username" label="用户名" header-align="center" fixed></el-table-column>
        <el-table-column prop="ctime" width="100" label="创建时间" header-align="center" :formatter="formatterColumn"></el-table-column>
        <el-table-column prop="role" width="100" label="用户角色" header-align="center"></el-table-column>
        <el-table-column prop="factory" label="分厂" header-align="center"></el-table-column>
        <el-table-column label="操作" fixed="right" header-align="center" width="150">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="handleEdit(scope.$index, scope.row)">管理</el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        layout="total, prev, pager, next"
        :total="userAmount"
        :small="sizesmall"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import { mapActions } from 'vuex'
  import * as api from 'api/manager'
  import moment from 'moment'
  
  export default {
    name: "",
    data() {
      return {
        username: '',
        currentPage: 1,
        pageSize: 10,
        userData: [],
        userAmount: '',
        loading: true
      }
    },
    props: {
      size: {
        type: String
      },
      sizesmall: {
        type: Boolean
      },
      twHeight: {
        type: Number
      }
    },
    methods: {
      ...mapActions([ 'setConfig' ]),
      search() {
        api.SearchUserInfo.r(this.username, 1, 10).then((res) => {
          if (res.data.success === 'SUCCESS') {
            // console.log(res)
            this.$router.push(`/manager?currentPage=1&search=${this.username}`)
            this.currentPage = 1
            this.userAmount = parseInt(res.data.data.totalCount)
            this.userData = this._formatOrigin(res.data.data.userList)
          }
        })
      },
      handleEdit(index, row) {
        // 如果直接带路由找信息的话，就不用设置公共变量了
        this.setConfig(row)
        this.$router.push(`/config?userId=${row.userId}&currentPage=${this.currentPage}&search=${this.username}`)
        /*console.log(index, row);
        let id = row.templateStock.templateId
        let amount = row.templateStock.templateAmount
        this.setTemplateId(id)
        this.setTemplateAmount(amount)
        this.$router.push('/handle')*/
      },
      handleDelete(index, row) {
        this.$confirm('确认删除？', "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: 'info',
          center: true
        }).then(() => {
          api.DeleteUser.r(row.userId).then(({ data }) => {
            this.$router.push(`/manager?currentPage=${this.currentPage}&search=${this.username}`)
            api.SearchUserInfo.r(this.username, this.currentPage, 10).then((res) => {
              if (res.data.success === 'SUCCESS') {
                // console.log(res)
                this.loading = false
                this.userAmount = parseInt(res.data.data.totalCount)
                this.userData = this._formatOrigin(res.data.data.userList)
              }
            })
          }).catch(err => {
            this.$message.error('未删除，服务器错误')
            console.log(err)
          })
        }).catch(() => {})
      },
      handleCurrentChange(currentPage) {
        this.currentPage = currentPage
        this.$router.push(`/manager?currentPage=${currentPage}&search=${this.username}`)
        api.SearchUserInfo.r(this.username, this.currentPage, 10).then((res) => {
          if (res.data.success === 'SUCCESS') {
            // console.log(res)
            this.loading = false
            this.userAmount = parseInt(res.data.data.totalCount)
            this.userData = this._formatOrigin(res.data.data.userList)
          }
        })
      },
      _formatOrigin(list) {
        var res = []
        list.forEach(item => {
          let obj = {}
          obj.userId = item.userInfo.id
          obj.username = item.userInfo.userName
          obj.factory = item.userInfo.userFactory
          obj.ctime = item.userInfo.userCtime
          obj.role = item.roleInfos[0].roleName || ''
          res.push(obj)
        })
        return res
      },
      formatterColumn(row, column) {
        var date = row[column.property]
        if (date == undefined) {
          return ""
        }
        return moment(date).format("YYYY-MM-DD HH:mm:ss")
      }
    },
    created() {
      this.currentPage = parseInt(this.$route.query.currentPage) || 1
      this.username = this.$route.query.search || ''
      api.SearchUserInfo.r(this.username, this.currentPage, 10).then((res) => {
        if (res.data.success === 'SUCCESS') {
          // console.log(res)
          this.loading = false
          this.userAmount = parseInt(res.data.data.totalCount)
          this.userData = this._formatOrigin(res.data.data.userList)
          this.$router.push(`/manager?currentPage=${this.currentPage}&search=${this.username}`)
          // 做逻辑判断，如果 请求当前页 大于 userAmount/10，那么请求最后一页
          /*let lastPage = parseInt(this.userAmount / 10)
          if (this.$route.query.currentPage > lastPage) {
            this.currentPage = lastPage
            this.$router.push(`/manager?currentPage=${this.currentPage}&search=${this.username}`)
          }*/
        }
      })
    },
    mounted() {
    
    }
  }
</script>

<style type="text/stylus" lang="stylus" rel="stylesheet/stylus" scoped>
  @import "~assets/stylus/variable.styl"
  @import "User.styl"
</style>



