<template>
	<div class="manager">
    <el-card class="table-wrapper" ref="tableWrapper">
      <div slot="header" class="clearfix">用户角色配置
        <!--<el-button type="primary" @click="enterUser">用户角色配置</el-button>
        <el-button type="primary" @click="enterAuth">用户权限配置</el-button>-->
      </div>
      <user :size="size" :sizesmall="sizesmall" :twHeight="twHeight" v-if="isUser"></user>
    </el-card>
  </div>
</template>

<script>
  import User from './User'
  export default {
    name: "",
    
    data() {
      return {
        isUser: true,
        size: 'medium',
        sizesmall: false,
        twHeight: 0
      }
    },
    methods: {
      enterUser() {
        this.isUser = true
      },
      enterAuth() {
        this.isUser = false
      }
    },
    components: {
      User
    },
    created() {
      this.size = (window.innerWidth < 767) ? 'mini': 'medium'
      this.sizesmall = (window.innerWidth < 767) ? true : false
    },
    mounted() {
      console.log('1')
      this.twHeight = this.$refs.tableWrapper.$el.clientHeight
    }
  }
</script>

<style type="text/stylus" lang="stylus" rel="stylesheet/stylus" scoped>
  @import "~assets/stylus/variable.styl"
  @import "Manager.styl"
</style>



